const ZOOM = 1;
const MAX_PREVIEW_WIDTH = 208 / ZOOM;
const MAX_PREVIEW_HEIGHT = 208 / ZOOM;

const calculatePreviewSize = ({width, height}) => {
    const ratio = height / width;
    const h1 = Math.min(ratio * MAX_PREVIEW_WIDTH, MAX_PREVIEW_HEIGHT);
    const w = Math.min(h1 / ratio, MAX_PREVIEW_WIDTH);
    const h = Math.min(w * ratio);
    return {width: w, height: h};
};

