const FIELD_TYPES = [
    'geo',
    'numeric',
    'percent',
    'dollars',
    'minutes',
    'hours',
    'nominal',
    'ordinal'
];

const formatNum = (minDigits: number, maxDigits: number, num: number) : string => {
    const options = {maximumFractionDigits: maxDigits, minimumFractionDigits: minDigits};
    return Number(num).toLocaleString(undefined, options);
};

const justAddCommas = curry(formatNum)(undefined, undefined);
const removeDecimals = curry(formatNum)(undefined, 0);
const oneDecimalPlace = curry(formatNum)(undefined, 1);
const twoDecimalPlaces = curry(formatNum)(undefined, 2);
const dollarsAndCents = curry(formatNum)(2, 2);

export const FIELD_TYPE_FORMAT_VALUES_MAP: {[name: string]: (value: any) => string} = {
    'geo': _ => { throw new Error('Should not be asked to format value') },
    'numeric': justAddCommas,
    'percent': (value) => oneDecimalPlace(value) + '%',
    'dollars': (value) => '$' + (value >= 1000 ? justAddCommas(value) : dollarsAndCents(value)),
    'minutes': (value) => removeDecimals(value) + ' minutes',
    'hours': (value) => removeDecimals(value) + ' hours',
    'years': (value) => removeDecimals(value) + ' years',
};

