import React from "react";
import {animated, useSpring} from 'react-spring';
import {scaleLinear, scalePower} from "@visx/scale";
import useSelect from "../hooks/useSelect";
import {FeatureRanges} from "./config";

const paddingPct = 0.1;

export type BubblePlotProps = {
    data: any[],
    featureRanges: FeatureRanges
    showLabel?: string
    disableInteraction?: boolean
    width: number
    height: number
};

const BubblePlot = ({
    data,
    featureRanges,
    showLabel = null,
    disableInteraction = false,
    width,
    height
}: BubblePlotProps) => {
    const padding = Math.min(width, height) * paddingPct;
    const xScale = scaleLinear<number>({
        domain: featureRanges.x,
        range: [padding, width - padding],
        round: true
    });
    const yScale = scaleLinear<number>({
        domain: featureRanges.y,
        range: [height - padding / 2, padding],
        round: true
    });

    const sizeScale = scalePower<number>({
        domain: [featureRanges.z[0] * 80, featureRanges.z[1] * 120],
        range: [0.01 * height, 0.06 * height],
        exponent: 0.5
    });
    const items = data
        .filter(v => v.x != null && v.y != null && v.z != null)
        .map(v => ({
            ...v,
            scaledX: xScale(v.x),
            scaledY: yScale(v.y),
            size: sizeScale(v.z * 100)
        }));

    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name'], showLabel, disableInteraction);
    return (
        <>
            <svg width={width} height={height}>
                {items.map(item => {
                    // eslint-disable-next-line react-hooks/rules-of-hooks
                    const [{animX, animY, animSize}, setAnim] = useSpring(() => ({
                        animX: item.scaledX,
                        animY: item.scaledY,
                        animSize: item.size
                    }));

                    setAnim({
                        animX: item.scaledX,
                        animY: item.scaledY,
                        animSize: item.size
                    })

                    return makeSelectableAndInteractive([item.name],
                        <animated.circle
                            cx={animX}
                            cy={animY}
                            r={animSize}
                            fill={item.color + 'EE'}
                            stroke={item.color}
                        />
                    );
                })}
            </svg>
            <Tooltip />
        </>
    );
};

export default BubblePlot;
