import React from "react";
import {scaleBand, scaleLinear} from "@visx/scale";
import useSelect from "../hooks/useSelect";
import {animated, useSpring} from 'react-spring';
import {range} from 'ramda';
import {FeatureRanges, PCT_V_SPACE_LEFT_FOR_LEGEND} from "./config";

const hPaddingPct = 0.015;
const vPaddingPct = 0.02

export type BarPlotProps = {
    data: any[]
    featureRanges: FeatureRanges
    showLabel?: string
    disableInteraction?: boolean
    width: number
    height: number
};

const getHeightAdjustment = (featureRanges: FeatureRanges, height: number, startAtZero: boolean = true) : number => {
    const domain = featureRanges.y[1] - (startAtZero ? 0 : featureRanges.y[0]);
    const pctOfLowest = featureRanges.y[0] / domain;
    const range = 1 - PCT_V_SPACE_LEFT_FOR_LEGEND;
    return height - Math.min(height, height * (range / pctOfLowest));
};

const BarPlot = ({
    data,
    featureRanges,
    showLabel = null,
    disableInteraction = false,
    width,
    height
}: BarPlotProps) => {
    const hPadding = React.useMemo(() =>
        Math.min(width, height) * hPaddingPct,
        [width, height]
    );
    const vPadding = React.useMemo(() =>
        Math.min(width, height) * vPaddingPct,
        [width, height]
    );
    const heightAdjustment = getHeightAdjustment (featureRanges, height);
    const yScale = React.useMemo(() => scaleLinear<number>({
        domain: [0, featureRanges.y[1]],
        range: [height - vPadding, heightAdjustment + vPadding],
        round: true
    }), [height, heightAdjustment, vPadding, featureRanges]);
    const tempItems: any[] = data
        .map(v => ({
            ...v,
            scaledY: yScale(v.y)
        }))
        .sort((a, b) => a.scaledY - b.scaledY);
    const xScale = React.useMemo(() => scaleBand<number>({
        range: [hPadding, width - hPadding],
        round: false,
        domain: range(0, tempItems.length),
        padding: 0.25,
    }), [tempItems, hPadding, width]);
    const items = tempItems.map((item, i) => ({
        ...item,
        scaledX: xScale(i)
    }))
    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name'], showLabel, disableInteraction);

    return (
        <>
            <svg width={width} height={height - vPadding}>
                {items.map(item => {
                    // eslint-disable-next-line react-hooks/rules-of-hooks
                    const [{ animY }, setAnim] = useSpring(() => ({
                        animY: item.scaledY
                    }));

                    setAnim({
                        animY: item.scaledY
                    });

                    return makeSelectableAndInteractive([item.name],
                        <animated.rect
                            y={animY.to(x => x)}
                            height={animY.to(x => height - x)}
                            x={item.scaledX}
                            width={xScale.bandwidth()}
                            fill={item.color}
                        />
                    )
                })}
            </svg>
            <Tooltip />
        </>
    );
};

export default BarPlot;
