import React from 'react';
import {useLocation} from 'react-router-dom';
import {useAppDispatch, useAppSelector} from "../app/hooks";
import {selectControlFields, selectControlFieldValues} from "./selectors";
import {selectYear, setYear} from "../features/year/yearSlice";
import {map, filter,} from 'ramda';
import {selectPlotType, setPlotType} from "../features/plotType/plotTypeSlice";
import {setControls, setLoaded} from "../features/controls/controlsSlice";
import {setSearchPart} from "./utils/url";
const queryString = require('query-string');

const Url = () => {
    const controls = useAppSelector(selectControlFieldValues);
    const storedYear = useAppSelector(selectYear);
    const storedPlotType = useAppSelector(selectPlotType);
    const dispatch = useAppDispatch();
    const {search} = useLocation();

    // Don't think we need the thunk stuff anymore, but just leave it for now.

    const updateUrl = () => {
        return (dispatch, getState) => {
            const state = getState();
            const controls = selectControlFields(state);
            const storedYear = selectYear(state);
            const storedPlotType = selectPlotType(state);
            if (state.controls.loaded) {
                const allParams = {
                    plot: storedPlotType.name,
                    ...map(f => f ? f.id : null, controls),
                    compare: controls.compare.map(c => c.id),
                    year: storedYear
                };
                const params = filter(p => !!p, allParams);
                const newS = queryString.stringify(params, {arrayFormat: 'bracket'});
                if (newS !== search) {
                    setSearchPart(newS);
                    // TODO: Should we really clobber all the history?
                    // window.history.replaceState({}, null, '?' + s);
                }
            }
        }
    }

    const configUpdated = () => {
        const { x, y, z, area, level, compare, plot, year } =
            queryString.parse(search, {arrayFormat: 'bracket'});
        const nonNullControls = filter(v => !!v, {x, y, z, area, level, compare});
        dispatch(setControls(nonNullControls));
        if (!!plot)
            dispatch(setPlotType(plot));
        if (!!year)
            dispatch(setYear(parseInt(year)));
    }

    const loadState = () => {
        return (dispatch, getState) => {
            dispatch(updateUrl());
            dispatch(setLoaded());
        }
    }

    React.useEffect(() => {  // Only first load
        dispatch(loadState());
    }, []);

    React.useEffect(() => {   // On URL change
        configUpdated();
    }, [search]);

    React.useEffect(() => {   // On controls change
        dispatch(updateUrl());
    }, [dispatch, controls, storedYear, storedPlotType]);

    return null
};

export default Url
