import {intersection, reduce, tail} from 'ramda';

// bind ::  [a] -> (a -> [b]) -> [b]
const bind = xs => f => xs.flatMap(f);

// foldr :: (a -> b -> b) -> b -> [a] -> b
const foldr = f => a => xs =>
    xs.reduceRight((a, x) => f(x)(a), a);

export const cartProdN = foldr(
    xs => as =>
        bind(as)(
            x => bind(xs)(
                a => [
                    [a].concat(x)
                ]
            )
        )
)([
    []
]);

// TODO: Are these (above and below) the same?

export function cartProduct<T>(...sets: T[][]) : T[][] {
    return sets.reduce((acc: T[][], set: T[]) =>
            acc.flatMap((x) => set.map(y => [...x, y])),
        [[]]);
}

export const intersectAll = (xs) => reduce(intersection, xs[0], tail(xs));

export const allValuesNotNull = (obj: Object) : boolean =>
    Object.values(obj).every(f => f != null);

export const flattenArrayOfObjects = (arr: Object[]) : Object => {
    const flatObject = {};
    for(let i = 0; i < arr.length; i++) {
        for(const property in arr[i]){
            flatObject[`${property}`] = arr[i][property];
        }
    }
    return flatObject;
};

export function firstAndLast<T>(values: T[]) : T[] {
    return [values[0], values[values.length - 1]];
}

export function nestedFirstAndLast<T>(values: T[][]) : T[][] {
    return values.map(firstAndLast);
}

export function capitalize(s: string) : string {
    return s[0].toUpperCase() + s.slice(1);
}

export const sum = (array) => array.reduce((a, b) => a + b);
export const average = (array) => sum(array) / array.length;
