import {
    DerivedDataValueSet,
    FeatureRanges,
    LegendCorner,
    LegendPosition,
    PlotType
} from "../config";
import {max, min} from 'd3-array';
import {ColorPalette, getColorFromPalette} from "./colors";
import {average} from "./misc";

export const processItemValues = (
    vs: DerivedDataValueSet[],
    controlFields,
    divisions,
    colorPalette: ColorPalette
) => {
    const createName = (v: DerivedDataValueSet) => {
        const cs = controlFields.compare;
        const div = divisions[v.geoid];
        const area = controlFields.area;
        const divName = cs.length > 0 ? div?.abbrev || div?.name : div?.name;
        const areaName = cs.length > 0 ? area.abbrev : area.name;
        const place = divName || areaName;
        const getC = c => {
            if (!c.values[v[c.id]]) {
                throw new Error(`Compare value '${c.id}' not found in ${place}`);
            }
            return place.length <= 5 ?
                c.values[v[c.id]].fullName : c.values[v[c.id]].shortName
        }
        const parts = [
            place,
            cs.map(getC).join(', ')
        ];
        return parts.filter(x => x.length > 0).join(': ')
    };
    return vs.map(v => ({
        ...v,
        name: createName(v),
        x: v[controlFields.x.id],
        y: v[controlFields.y.id],
        z: v[controlFields.z.id],
        color: getColorFromPalette(colorPalette, controlFields.compare, v)
    }));
};

/**
 * Assumes a linear scale
 */
const _findClearestCorner = (left: number[],
                             right: number[],
                             y_mean: number,
                             forceCorner?: LegendCorner) : LegendPosition => {
    const topLeft = (max(left) - y_mean) / y_mean;
    const topRight = (max(right) - y_mean) / y_mean;
    // Avoid lower corners by tipping the scale a little
    const bottomLeft = ((y_mean * 2 - min(left)) / y_mean);
    const bottomRight = ((y_mean * 1.5 - min(right)) / y_mean);
    const minimum = min([topLeft, topRight, bottomLeft, bottomRight]);
    if (forceCorner != null) {
        if (forceCorner === LegendCorner.NW) {
            return {corner: LegendCorner.NW, closestDataPoint: topLeft };
        } else if (forceCorner === LegendCorner.NE) {
            return {corner: LegendCorner.NE, closestDataPoint: topRight };
        } else if (forceCorner === LegendCorner.SW) {
            return {corner: LegendCorner.SW, closestDataPoint: bottomLeft };
        } else if (forceCorner === LegendCorner.SE) {
            return {corner: LegendCorner.SE, closestDataPoint: bottomRight };
        }
    }
    if (topLeft === minimum) {
        return {corner: LegendCorner.NW, closestDataPoint: topLeft };
    } else if (topRight === minimum) {
        return {corner: LegendCorner.NE, closestDataPoint: topRight };
    } else if (bottomLeft === minimum) {
        return {corner: LegendCorner.SW, closestDataPoint: bottomLeft };
    } else if (bottomRight === minimum) {
        return {corner: LegendCorner.SE, closestDataPoint: bottomRight };
    } else {
        return {corner: LegendCorner.NW, closestDataPoint: topLeft };
    }
};

export const chooseLegendPosition = (plotType: PlotType,
                                     featureRanges: FeatureRanges,
                                     data: any[],
                                     forceCorner?: LegendCorner) : LegendPosition => {
    const _splitHalfs = (pred: (any, number) => number,
                         values: any[],
                         c1: number,
                         c2: number) : [number[], number[]] => {
        return [
            values.filter((v, i) => pred(v, i) < c1).map(v => v.y),
            values.filter((v, i) => pred(v, i) > c2).map(v => v.y)
        ];
    };
    const y_mean = average(featureRanges.y);
    if (plotType.name === 'bubble') {
        const c = average(featureRanges.x);
        const x_min = featureRanges.x[0];
        const x_max = featureRanges.x[1];
        const c14 = (c + x_min) / 2;
        const c34 = (c + x_max) / 2;
        return _findClearestCorner(..._splitHalfs(d => d.x, data, c14, c34), y_mean, forceCorner);
    } else if (plotType.name === 'line') {
        const c = average(featureRanges.year);
        return _findClearestCorner(..._splitHalfs(d => d.year, data, c, c), y_mean, forceCorner);
    } else if (plotType.name === 'bar') {
        const sortedData = data.sort((a, b) => a.scaledY - b.scaledY);
        const c = data.length / 2;
        return _findClearestCorner(..._splitHalfs((_, i) => i, sortedData, c, c), y_mean, forceCorner);
    } else {
        throw new Error('Unhandled plot type: ' + plotType.name);
    }
};
