import {FC} from 'react';
import './Legend.scss';
import {useAppSelector} from "../app/hooks";
import {
    selectColorPalette,
    selectCurrentCompareDetails,
    selectFeatureRanges,
    selectLegendPosition,
    selectPlotData,
} from "./selectors";
import {ColorPalette} from "./utils/colors";
import {CompareByField, LegendCorner, LegendPosition} from "./config";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import {chooseLegendPosition} from "./utils/domain";

type LegendComponentProps = {
    compares: CompareByField[]
    colorPalette: ColorPalette
}

const bgAlphaHex = 'CC';

const OrdinalStrip = ({fromColor, toColor, label=null}) => {
    return (
        <>
            <div className='strip'
                 style={{
                     backgroundImage: `linear-gradient(to right, ${fromColor + bgAlphaHex}, ${toColor + bgAlphaHex})`
                 }}>
                {label}
            </div>
        </>
    )
}

const OrdinalLabels = ({start, end}) => (
    <div className='labels strip'>
        <div className='label' style={{ top: 0, left: 0 }}>{start}</div>
        <div className='label' style={{ top: 0, right: 0 }}>{end}</div>
    </div>
)

const OrdinalLegend = ({colorPalette}: LegendComponentProps) => {
    return (
        <div className='ordinal'>
            <OrdinalLabels start={colorPalette.labels.cols[0]}
                           end={colorPalette.labels.cols[1]} />
            <OrdinalStrip fromColor={colorPalette.colors.byRowAndCol[0][0]}
                          toColor={colorPalette.colors.byRowAndCol[0][1]}
            />
        </div>
    );
};

const NominalLegend = ({colorPalette}: LegendComponentProps) => {
    // const cols = [
    //     {name: 'Wh', hue: 200},
    //     {name: 'Bl', hue: 340},
    //     {name: 'Hs', hue: 150},
    //     {name: 'As', hue: 40},
    // ];

    return (
        <div className='nominal'>
            {colorPalette.colors.byRowAndCol.map((row, r) =>
                <div key={'row-' + r}
                     className='strip'
                     style={{ backgroundColor: row[0] + bgAlphaHex }}>
                    {colorPalette.labels.rows[r]}
                </div>
            )}
        </div>
    );
};

const OrdinalByNominalLegend = ({colorPalette}: LegendComponentProps) => (
    <div className='ordinal'>
        <OrdinalLabels start={colorPalette.labels.cols[0]}
                       end={colorPalette.labels.cols[1]} />
        {colorPalette.colors.byRowAndCol.map((colorRow, r) => (
            <OrdinalStrip key={'row-' + r}
                          fromColor={colorRow[0]}
                          toColor={colorRow[1]}
                          label={colorPalette.labels.rows[r]}
            />
        ))}
    </div>
);

const NominalByNominalLegend = ({colorPalette}: LegendComponentProps) => (
    <table className='nominal-grid' cellPadding={0} cellSpacing={0}>
        <tbody>
            <tr>
                <th />
                {colorPalette.labels.cols.map((col, c) => (
                    <th key={'col-header-' + c} className='col-header'>
                        {col}
                    </th>
                ))}
            </tr>
            {colorPalette.colors.byRowAndCol.map((colorRow, r) => (
                <tr key={'row-' + r} >
                    <th className='row-header'>
                        {colorPalette.labels.rows[r]}
                    </th>
                    {colorRow.map((colorCell, c) => (
                        <td key={`cell-${c}-${r}`}
                             className='cell'
                             style={{
                                 backgroundColor: colorCell + bgAlphaHex
                             }} />
                    ))}
                </tr>
            ))}
        </tbody>
    </table>
);

const OrdinalByOrdinalLegend = ({colorPalette}: LegendComponentProps) => {
    const cs = colorPalette.colors.byRowAndCol;
    const ls = colorPalette.labels;
    return (
        <div  className='ordinal'>
            <div className='layer'
                 style={{
                     backgroundImage: `linear-gradient(to right, ${cs[0][0] + bgAlphaHex}, ${cs[0][1] + bgAlphaHex})`,
                }}
            >
            </div>
            <div className='layer'
                 style={{
                     backgroundImage: `linear-gradient(to right, ${cs[1][0] + bgAlphaHex}, ${cs[1][1] + bgAlphaHex}`,
                     WebkitMask: 'linear-gradient(to bottom, #0000, #00000080 50%, #000D 80%, #000 100%)',
                     mask: 'linear-gradient(to bottom, #0000, #00000080 50%, #000D 80%, #000 100%)',
                 }}
            >
            </div>
            <div className='label' style={{ top: 4, left: 16}}>${ls.cols[0]}</div>
            <div className='label' style={{ top: 4, right: 10}}>${ls.cols[1]}</div>
            <div className='label' style={{ top: 16, left: 6 }}>${ls.rows[0]}</div>
            <div className='label' style={{ bottom: 10, left: 6 }}>${ls.rows[1]}</div>
        </div>
    );
};

const getLegendComponent = (compares: CompareByField[]) : FC<LegendComponentProps> => {
    const types = compares.map(c => c.type);
    switch (compares.length) {
        case 0:
            return null;
        case 1:
            return types[0] === 'nominal' ? NominalLegend : OrdinalLegend;
        case 2:
            if (types[0] === 'nominal') {
                return types[1] === 'nominal' ? NominalByNominalLegend : OrdinalByNominalLegend;
            } else {
                return types[1] === 'ordinal' ? OrdinalByOrdinalLegend : OrdinalByNominalLegend;
            }
        default:
            throw new Error(`Has ${compares.length} compares. Must be 0-2.`)
    }
};

const Legend = () => {
    const colorPalette = useAppSelector(selectColorPalette);
    const compares = useAppSelector(selectCurrentCompareDetails);
    const position = useAppSelector(selectLegendPosition);
    const LegendComponent = getLegendComponent(compares);
    if (!position || compares.length === 0) {
        return null;
    }
    return(
        <div className={`legend ${position.corner}`}>
            <LegendComponent colorPalette={colorPalette} compares={compares} />
        </div>
    );
}

export default Legend;
