import React from 'react';
import {Link} from 'react-router-dom';
import {
    AppBar, Collapse,
    Divider, Drawer,
    Hidden, IconButton,
    List,
    ListItemButton, ListItemIcon,
    ListItemText, Toolbar, Typography
} from "@mui/material";
import HomeIcon from "@mui/icons-material/Home";
import DataIcon from "@mui/icons-material/Storage";
import MenuIcon from "@mui/icons-material/Menu";
import LightModeIcon from "@mui/icons-material/LightMode";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import './Nav.scss';
import ShareIcon from "@mui/icons-material/Share";
import {useAppSelector} from "../app/hooks";
import {selectPlotPresets} from "./selectors";

const SideMenuItem = ({text, icon, children=null}) => {
    const [open, setOpen] = React.useState(false);
    return (
        <>
            <ListItemButton onClick={() => setOpen(!open)}>
                <ListItemIcon className='menu-item-icon'>{icon}</ListItemIcon>
                <ListItemText primary={text} />
                {children && (open ? <ExpandLess /> : <ExpandMore />)}
            </ListItemButton>
            {children && (
                <Collapse in={open} timeout="auto" unmountOnExit className='nested-menu'>
                    {children}
                </Collapse>
            )}
        </>
    );
};

const Nav = ({darkMode, ShareDialog, children=null}) => {
    const plotPresets = useAppSelector(selectPlotPresets);
    const [shareOpen, setShareOpen] = React.useState(false);
    const [drawerOpen, setDrawerOpen] = React.useState(false);
    const drawer = (
        <div className='side-menu'>
            <Divider />
            <List>
                <a href='/'>
                    <SideMenuItem text='Statistpedia' icon={<HomeIcon />} />
                </a>
                {/*<SideMenuItem text='Datasets' icon={<DataIcon />} />*/}
                <SideMenuItem text='Presets' icon={<DataIcon />}>
                    <List>
                        { plotPresets.map((p) => (
                            <Link to={'/' + p.url} key={p.title}>
                                <ListItemButton>
                                    <ListItemText>{p.title}</ListItemText>
                                </ListItemButton>
                            </Link>
                        ))}
                    </List>
                </SideMenuItem>
            </List>
            <Divider />
        </div>
    );
    return (
        <>
            <nav>
                <Hidden xlUp>
                    <Drawer
                        variant="temporary"
                        open={drawerOpen}
                        onClose={() => setDrawerOpen(false)}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
                <Hidden xlDown>
                    <Drawer
                        variant="permanent"
                        open
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
            </nav>
            <AppBar className='top-bar'
                    position="static"
                    elevation={darkMode.value ? 0 : 1}>
                <Toolbar variant="dense">
                    <IconButton edge="start"
                                sx={{
                                    marginRight: '0.5em',
                                    display: { xs: 'inline-flex', xl: 'none' }
                                }}
                                onClick={() => setDrawerOpen(!drawerOpen)}
                                color="inherit"
                                aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography
                        id='site-title'
                        variant="h1"
                        sx={{
                            display: ['none', 'block']
                        }}
                    >
                        <Link to='/' style={{
                            color: 'inherit',
                            textDecoration: 'none'
                        }}>
                            Statistipedia <sub><small><i>EXPLORE</i></small></sub>
                        </Link>
                    </Typography>
                    <Typography
                        id='site-title'
                        variant="h1"
                        sx={{
                            display: ['block', 'none']
                        }}
                    >
                        <Link to='/' style={{
                            color: 'inherit',
                            textDecoration: 'none'
                        }}>
                            S<sub><small><i>E</i></small></sub>
                        </Link>
                    </Typography>
                    <div className="grow" />
                    {children}
                    <IconButton className='menu-btn'
                                onClick={() => setShareOpen(true)}
                                color="inherit">
                        <ShareIcon />
                    </IconButton>
                    <IconButton edge="end"
                                className='menu-btn'
                                onClick={darkMode.toggle}
                                color="inherit">
                        {darkMode.value ? <LightModeIcon /> : <DarkModeIcon />}
                    </IconButton>
                    {shareOpen && <ShareDialog isOpen={shareOpen} setOpen={setShareOpen} />}
                </Toolbar>
            </AppBar>

        </>
    );
};

export default Nav;
