import PlotTypeSelect from "../plotType/PlotTypeSelect";
import {
    Autocomplete, Button,
    Divider, Drawer,
    FormControl, Input, InputAdornment,
    InputLabel, List, ListItem, ListItemText, Menu, MenuItem, MenuList,
    NativeSelect, Select, ListItemIcon,
    TextField, IconButton
} from "@mui/material";
import React, {ChangeEvent, useEffect} from "react";
import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {setControls} from "./controlsSlice";
import {selectPlotType} from "../plotType/plotTypeSlice";
import {
    selectAreas,
    selectAllCompareByFieldDetails,
    selectNumericFields, selectLevels
} from "../data/dataSlice";
import {selectControlFields} from "../../dashboard/selectors";
import {allValuesNotNull, capitalize} from "../../dashboard/utils/misc";
import {groupBy, curry, zipObj} from "ramda";
import {
    BOTTOM_DRAWER_CORNER,
    BOTTOM_DRAWER_HEIGHT, CompareByField,
    DataField
} from "../../dashboard/config";
import useMediaQuery from "@mui/material/useMediaQuery";
import {useTheme} from "@mui/system";
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import './Controls.scss';
import {FIELD_TYPE_ICONS_MAP} from "../../dashboard/icons";
import Cached from "@mui/icons-material/Cached";

type FieldOption = DataField & {
    disabled: boolean
}

type FieldGroup = {
    name: string,
    fields: FieldOption[]
};

const groupFields = (fields: any[]) : FieldGroup[] => {
    const gs = groupBy(f => f.group, fields);
    return Object.keys(gs).map(k => ({ name: k, fields: gs[k] }));
};

const DrawerFieldSelect = ({children, title, open, setOpen}) => {
    useEffect(() => {
        document.body.style.position = open ? 'fixed' : 'initial';
    }, [open]);

    return (
        <Drawer
            anchor='bottom'
            PaperProps={{
                style: {
                    height: BOTTOM_DRAWER_HEIGHT,
                    borderRadius: `${BOTTOM_DRAWER_CORNER}px ${BOTTOM_DRAWER_CORNER}px 0 0`
                }
            }}
            ModalProps={{
                keepMounted: true
            }}
            open={open}
            onClose={() => setOpen(false)}
        >
            <div>
                <div className='select-drawer-bar'>
                    <div className='title'>{title}</div>
                    <div className='actions'>
                        <Button onClick={() => setOpen(false)}>Close</Button>
                    </div>
                </div>
                <div className='select-drawer-content'>
                    {children}
                </div>
            </div>
        </Drawer>
    );
};

const MenuFieldSelect = ({children, anchorEl, setAnchorEl}) => (
    <Menu
        variant="menu"
        anchorEl={anchorEl}
        open={anchorEl != null}
        onClose={() => setAnchorEl(null)}
        MenuListProps={{
            role: 'listbox',
        }}
    >
        {children}
    </Menu>
);

const FieldSelect = ({children, title, anchorEl, setAnchorEl}) => {
    const theme = useTheme();
    const useDrawer = useMediaQuery(theme.breakpoints.down('md'));
    return useDrawer ? (
        <DrawerFieldSelect title={title}
                           open={anchorEl != null}
                           setOpen={() => setAnchorEl(null)}>
            {children}
        </DrawerFieldSelect>
    ) : (
        <MenuFieldSelect anchorEl={anchorEl}
                         setAnchorEl={setAnchorEl}>
            {children}
        </MenuFieldSelect>
    )
};

const BaseFieldSelect = ({field, options, onChange, disabled=false, label,
                             value, className=undefined} :
                             {field: string, options: FieldGroup[], onChange,
                                 disabled?: boolean, label: string,
                                 value: string, className?: string}) => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const handleClickListItem = (event) => {
        !disabled && setAnchorEl(event.currentTarget);
    };
    return (
        <div>
            <FormControl className="form-control" disabled={disabled}>
                <InputLabel variant="standard" htmlFor={`field-${field}`}>
                    {label}
                </InputLabel>
                <div className={`${disabled ? 'Mui-disabled ': ''}${className ? className + ' ' : ''}custom-select MuiInput-root MuiInput-underline MuiInputBase-root MuiInputBase-colorPrimary MuiInputBase-formControl MuiSelect-root`}
                     onClick={handleClickListItem}>
                    <div role="button" aria-expanded="false"
                         aria-haspopup="listbox" aria-labelledby="compare-by-1"
                         id="compare-by-1"
                         className="MuiSelect-select MuiSelect-standard MuiInput-input MuiInputBase-input">
                        {value}
                    </div>
                    <ArrowDropDownIcon className='adornment' />
                    <Input id={`field-${field}`}
                           className="MuiSelect-nativeInput css-yf8vq0-MuiSelect-nativeInput"
                           type='hidden'
                           value={value}
                           aria-hidden="true"
                    />
                </div>
            </FormControl>
            <FieldSelect title={label} anchorEl={anchorEl} setAnchorEl={setAnchorEl}>
                {options.flatMap((g, i) => [
                    i > 0 ? <Divider key={`${g.name}_divider`} /> : null,
                    <MenuItem disabled={true}
                              key={`${g.name}_header`}
                              className='menu-group-header'>
                        {g.name}
                    </MenuItem>,
                    ...g.fields.map(f => {
                        const Icon = FIELD_TYPE_ICONS_MAP[f.type] || FIELD_TYPE_ICONS_MAP.default;
                        return (
                            <MenuItem key={f.id}
                                      value={f.id}
                                      selected={f.name === value}
                                      disabled={f.disabled}
                                      onClick={() => onChange({[field]: f.id})}>
                                <ListItemIcon>
                                    <Icon/>
                                </ListItemIcon>
                                <ListItemText primary={f.name}
                                              secondary={f.description}/>
                            </MenuItem>
                        )
                    })
                ])}
            </FieldSelect>
        </div>
    );
};

const Controls = () => {
    const plotType = useAppSelector(selectPlotType);
    const controls = useAppSelector(selectControlFields);
    const controlsIsReady = allValuesNotNull(controls);
    const numericFields = useAppSelector(selectNumericFields);
    const compareByFields = useAppSelector(selectAllCompareByFieldDetails);
    const areas = useAppSelector(selectAreas);
    const levels = useAppSelector(selectLevels);
    const dispatch = useAppDispatch();

    const disableUsedCompares = (fields: CompareByField[]) : CompareByField[] => {
        const used = controls.compare.map(c => c.id);
        return fields.map(f => ({
            ...f,
            disabled: used.includes(f.id)
        }));
    };

    const addRecents = (fields: CompareByField[]) : CompareByField[] => {
        const none = {
            id: 'none',
            type: 'none',
            name: 'None',
            description: 'Clear field',
            group: 'Recently used',
            values: {}
        };
        return [none, ...fields];
    }

    const numericFieldGroups = groupFields(numericFields);
    const compareByGroups = groupFields(addRecents(disableUsedCompares(compareByFields)));

    const updateState = (newState: {[key: string]: any}) => {
        return dispatch(setControls(zipObj(Object.keys(newState), Object.values(newState))));
    };

    const updateCompare = (i: number, newState: {[key: string]: any}) => {
        const newNewState = newState;
        const k = Object.keys(newState)[0];
        const v = Object.values(newState)[0];
        if (v === 'none') {
            switch (i) {
                case 0: newNewState[k] = controls.compare.length === 2 ?
                    [controls.compare[1].id] : []; break;
                case 1: newNewState[k] = [controls.compare[0].id]; break;
                default: throw new Error('Unexpected compare index: ' + i)
            }
        } else {
            switch (i) {
                case 0: newNewState[k] = controls.compare.length === 2 ?
                    [v, controls.compare[1].id] : [v]; break;
                case 1: newNewState[k] = [controls.compare[0].id, v]; break;
                default: throw new Error('Unexpected compare index: ' + i)
            }
        }
        return updateState(newNewState);
    };

    const switchCompares = () => {
        const newState = {
            'compare': [controls.compare[1].id, [controls.compare[0].id]]
        };
        return(updateState(newState));
    };

    const CompareByFieldSelect = ({i}: {i: number}) => (
        <BaseFieldSelect field={`compare`}
                         className='compare-by'
                         options={compareByGroups}
                         onChange={curry(updateCompare)(i)}
                         disabled={plotType.name === 'map' || i > controls.compare.length}
                         label='Compare by'
                         value={controls.compare[i]?.name || ' '}
        />
    );

    const FieldSelect = ({field} : {field: string}) => (
        <BaseFieldSelect field={field}
                         options={numericFieldGroups}
                         onChange={updateState}
                         disabled={!plotType.fields[field]}
                         label={plotType.fields[field] || `${field.toUpperCase()}-Axis`}
                         value={controls[field].name}
        />
    );

    const BasicSelect =
        ({field, options, disabled=false,
             onChange=(e) => updateState({[field]: e.target.value})} :
             {field: string, options: {allIds: string[], byId}, disabled?: boolean,
             onChange?: (e: ChangeEvent<HTMLSelectElement>) => void}) => (
        <FormControl className="form-control" disabled={disabled}>
            <InputLabel variant="standard" htmlFor={`field-${field}`}>
                {capitalize(field)}
            </InputLabel>
            <NativeSelect id={field}
                          onChange={onChange}
                          value={controls[field].id}>
                {options.allIds.map(id => (
                    <option key={id} value={id}>{options.byId[id].name}</option>
                ))}
            </NativeSelect>
        </FormControl>
    )

    // Not working
    const AutocompleteSelect =
        ({field, options, disabled=false} :
             {field: string, options: {allIds: string[], byId}, disabled?: boolean}) => (
        <Autocomplete
            freeSolo
            disabled={disabled}
            id={field}
            className="form-control"
            options={options.allIds}
            value={controls[field].id}
            getOptionLabel={(option) => options.byId[option].name}
            renderInput={(params) =>
                <TextField {...params} variant="standard" label={capitalize(field)} />}
        />
    )

    return controlsIsReady && areas.allIds.length > 0 && (
        <div className='controls'>
            <PlotTypeSelect />
            <BasicSelect field='area'
                         options={areas}
                         onChange={(e) => {
                             const v = e.target.value;
                             updateState({
                                 'area': v,
                                 'level': v === 'us' ? 'state' : 'county'
                             });
                         }} />
            <BasicSelect field='level' options={levels} disabled={true} />
            <div className='compare-by-fieldset'>
                <div className='fields'>
                    <CompareByFieldSelect i={0} />
                    <CompareByFieldSelect i={1} />
                </div>

                <div className='switch'>
                    <IconButton size='small'
                                disabled={controls.compare.length < 2}
                                onClick={switchCompares}>
                        <Cached />
                    </IconButton>
                </div>
            </div>
            <FieldSelect field='x' />
            <FieldSelect field='y' />
            <FieldSelect field='z' />
        </div>
    );
};

export default Controls;
