import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import masterData from '../../dashboard/data/master.json';
import {ControlValues} from "../../dashboard/config";
import {equals} from 'ramda';

export interface ControlsState {
    value: ControlValues,
    loaded: boolean
}

const defaultFields = Object.keys(masterData.fields);

const initialState: ControlsState = {
    value: {
        area: "us",
        compare: [],
        level: "state",
        x: defaultFields[0],
        y: defaultFields[1],
        z: "pct_total_pop"
    },
    loaded: false
};

export const controlsSlice = createSlice({
    name: 'controls',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        setControls: (state, action: PayloadAction<Object>) => {
            const newValue = {...state.value, ...action.payload};
            if (!equals(state.value, newValue)) {
                state.value = newValue;
            }
        },
        setLoaded: (state) => {
            state.loaded = true;
        }
    }
});

export const { setControls, setLoaded } = controlsSlice.actions;

export const selectActualControlValues = (state: RootState) : ControlValues =>
    state.controls.value;

export const selectControlsLoaded = (state: RootState) : boolean =>
    state.controls.loaded;

export default controlsSlice.reducer;
