import {
    CompareByField,
    CompareByFields,
    CompareByFieldValue
} from "../config";
import {firstAndLast, nestedFirstAndLast} from "./misc";
import {curry, compose, zipObj} from 'ramda';

type ColorHsv = {
    h: number,
    s: number,
    v: number
}

const DEFAULT_COLOR: ColorHsv = {h: 214, s: 0.8, v: 1};

const HUE_PRESETS = [
    210,
    350,
    32,
    285,
    160
];

const SAT_RANGE = [0.3, 1];

// Generates a single color for a compare feature value
type ColorFn = (inputColor: ColorHsv,
                value: CompareByFieldValue,
                nFieldValues: number,
                valueIndex: number,
                isPrioritized: boolean) => ColorHsv

export const autoHueScale = (c: ColorHsv, n: number, i: number) : ColorHsv  => {
    return {...c, h: HUE_PRESETS[i]};
};

export const autoSatScale = (c: ColorHsv, n: number, i: number) : ColorHsv  => {
    const s = ((SAT_RANGE[1] - SAT_RANGE[0]) / n) * i + SAT_RANGE[0];
    return {...c, s, v: s >= 1/2 ? 1 - s/3 : 1 };
};

export const autoScaleColorNominal = (
    c, value, nFieldValues, index, isPrioritized
) : ColorHsv => {
    if (isPrioritized) {
        return autoHueScale(c, nFieldValues, index);
    } else {
        // Assume ascending order
        const sStart = SAT_RANGE[0];
        const sEnd = 1;
        const vStart = 1;
        const vEnd = 0.5;
        const sStep = (sEnd - sStart) / nFieldValues;
        const vStep = (vEnd - vStart) / nFieldValues;
        return {...c, s: sStart + sStep * index, v: vStart + vStep * index};
    }
};

export const autoScaleColorOrdinal = (
    c, value, nFieldValues, index, isPrioritized
) : ColorHsv => {
    return autoSatScale(c, nFieldValues, index);
};

const COMPARE_BY_COLOR_FNS: {[id: string]: ColorFn} = {  // Listed in priority order
    'sex':  (c, value) =>
        ({...c, h: value.shortName === 'M' ? 210 : 330}),
    'race': autoScaleColorNominal,
    'marital': autoScaleColorNominal,
    'education': autoScaleColorNominal,
    'age': autoScaleColorOrdinal,
};

const COMPARE_BY_COLOR_PRIORITIES : {[key: string]: number} = {
    'sex': 1,
    'race': 2,
    'marital': 2,
    'education': 2,
    'age': 3
};

function hsvToRgb(h, s, v) {
    let r, g, b;
    const h1 = h / 360;  // Convert hue to a value between 0 and 1
    const i = Math.floor(h1 * 6);
    const f = h1 * 6 - i;
    const p = v * (1 - s);
    const q = v * (1 - f * s);
    const t = v * (1 - (1 - f) * s);
    switch (i % 6) {
        case 0: r = v; g = t; b = p; break;
        case 1: r = q; g = v; b = p; break;
        case 2: r = p; g = v; b = t; break;
        case 3: r = p; g = q; b = v; break;
        case 4: r = t; g = p; b = v; break;
        case 5: r = v; g = p; b = q; break;
    }
    return [ r * 255, g * 255, b * 255 ].map(x => Math.round(x));
}

function componentToHex(c) {
    // @ts-ignore
    const hex = c.toString(16);
    return hex.length === 1 ? "0" + hex : hex;
}

function rgbToHex(r, g, b) {
    return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}

export const hsvToHex = (h, s, v) : string => {
    const [r, g, b] = hsvToRgb(h, s, v);
    return rgbToHex(r, g, b);
};

type FieldValueKey = {
    field: string
    value: string
}

type ColorKeySet = {
    k1?: FieldValueKey
    k2?: FieldValueKey
}

type KeysAndHsvColor = {
    keys: ColorKeySet
    color: ColorHsv
}

type ColorPaletteHashMap = { [key: string]: string }

export type ColorPalette = {
    hashMap: ColorPaletteHashMap
    labels: {
        rows: string[],
        cols: string[]
    },
    colors: {
        byRowAndCol: string[][]
    }
}

const sortKeys = (cbfs: CompareByField[], keys: ColorKeySet) : ColorKeySet => {
    if (keys.k2) {  // If it has 2 keys (0 or 1 and the order doesn't matter)
        const fieldToIndexMap = zipObj(
            cbfs.map((cbf) => cbf.id),
            cbfs.map((_, i) => i)
        );
        if (fieldToIndexMap[keys.k1.field] > fieldToIndexMap[keys.k2.field]) {
            return { k1: keys.k2, k2: keys.k1 };  // Switch order
        }
    }
    return keys;  // Just return original order
};

const createKey = (cbfs: CompareByField[], keys: ColorKeySet) : string => {
    if (!keys.k1) {
        return ''
    } else {
        const sk = sortKeys(cbfs, keys);
        return sk.k1.field + '=' + sk.k1.value +
        (sk.k2 ? (';' + sk.k2.field + '=' + sk.k2.value) : '')
    }
}

const addColorToPalette = (p: ColorPaletteHashMap,
                           cbfs: CompareByField[],
                           keys: ColorKeySet, color: string) => {
    p[createKey(cbfs, keys)] = color;
}

const createKeySet = (compareByFields: CompareByField[],
                      value: Object) : ColorKeySet => {
    const cbfs = Object.values(compareByFields);
    if (cbfs.length === 0) {
        return {};
    }
    const k = { k1: { field: cbfs[0].id, value: value[cbfs[0].id] }};
    switch (cbfs.length) {
        case 1: return k;
        case 2: return { ...k, k2: { field: cbfs[1].id, value: value[cbfs[1].id] }};
        default: throw new Error('Invalid compare by field length: ' + cbfs.length);
    }
};

const _getColorFromPalette = (p: ColorPaletteHashMap,
                              cbfs: CompareByField[],
                              ks: ColorKeySet) : string => {
    return p[createKey(cbfs, ks)];
}

export const getColorFromPalette = (p: ColorPalette,
                                    cbfs: CompareByField[],
                                    value: Object) : string =>
    _getColorFromPalette(p.hashMap, cbfs, createKeySet(cbfs, value))

const getShortLabels = (compare: CompareByField) : string[] =>
    Object.values(compare.values).map(v => v.shortName);

const getFullLabels = (compare: CompareByField) : string[] =>
    Object.values(compare.values).map(v => v.fullName);

function iterateOverCompareGrid<T>(fn: (ks: ColorKeySet) => T,
                                   cbf1: CompareByField,
                                   cbf2: CompareByField) : T[][] {
    return Object.values(cbf1.values).map((v1) =>
        Object.values(cbf2.values).map((v2) => fn({
            k1: {field: cbf1.id, value: v1.shortName},
            k2: {field: cbf2.id, value: v2.shortName}
        })));
}

const switchToAlwaysMakeLegendHorizontal = (cbfs: CompareByField[]) : CompareByField[] =>
    Object.values(cbfs[1].values).length >= Object.values(cbfs[0].values).length
        ? cbfs : [cbfs[1], cbfs[0]];

const setupColorPalette = (cbfs: CompareByField[],
                           kahcs: KeysAndHsvColor[]) : ColorPalette => {
    const p = { hashMap: {} };
    kahcs.forEach((kahc) => {
        addColorToPalette(
            p.hashMap,
            cbfs,
            kahc.keys,
            hsvToHex(kahc.color.h, kahc.color.s, kahc.color.v));
    });
    const keySets = kahcs.map(kahc => kahc.keys);
    const getColor = curry(_getColorFromPalette)(p.hashMap, cbfs);
    const getColorGrid = curry(iterateOverCompareGrid)(getColor);
    switch (cbfs.length) {
        case 0:
            return {
                ...p,
                labels: { rows: [], cols: [] },
                colors: { byRowAndCol: [] }
            }
        case 1:
            return cbfs[0].type === 'nominal' ? {
                ...p,
                labels: {
                    rows: getFullLabels(cbfs[0]),
                    cols: []
                },
                colors: {
                    byRowAndCol: keySets.map(compose(x => [x], getColor))
                }
            } : {
                ...p,
                labels: {
                    rows: [],
                    cols: firstAndLast(getShortLabels(cbfs[0])),
                },
                colors: {
                    byRowAndCol: [firstAndLast(keySets.map(getColor))]
                }
            };
        case 2:
            if (cbfs[0].type === 'nominal') {  // nominal x nominal
                const scbfs = switchToAlwaysMakeLegendHorizontal(cbfs);
                return cbfs[1].type === 'nominal' ? {
                    ...p,
                    labels: {
                        rows: getShortLabels(scbfs[0]),
                        cols: getShortLabels(scbfs[1])
                    },
                    colors: {
                        byRowAndCol: getColorGrid(scbfs[0], scbfs[1])
                    }
                } : {  // nominal x ordinal
                    ...p,
                    labels: {
                        rows: getFullLabels(cbfs[0]),
                        cols: firstAndLast(getShortLabels(cbfs[1]))
                    },
                    colors: {
                        byRowAndCol: nestedFirstAndLast(getColorGrid(cbfs[0], cbfs[1]))
                    }
                }
            } else {
                return cbfs[1].type === 'ordinal' ? {  // ordinal x ordinal
                    ...p,
                    labels: {
                        rows: firstAndLast(getShortLabels(cbfs[0])),
                        cols: firstAndLast(getShortLabels(cbfs[1])),
                    },
                    colors: {
                        byRowAndCol: firstAndLast(nestedFirstAndLast(getColorGrid(cbfs[0], cbfs[1])))
                    }
                } : {  // ordinal x nominal
                    ...p,
                    labels: {
                        rows: getFullLabels(cbfs[1]),
                        cols: firstAndLast(getShortLabels(cbfs[0]))
                    },
                    colors: {
                        byRowAndCol: nestedFirstAndLast(getColorGrid(cbfs[1], cbfs[0]))
                    }
                };
            }
        default:
            throw new Error(`Has ${cbfs.length} compares. Must be 0-2.`)
    }
}

export const createColorPalette = (compareByFields: CompareByFields) : ColorPalette => {
    const cbfs = Object.values(compareByFields);
    const priorities = cbfs.map(cbf => COMPARE_BY_COLOR_PRIORITIES[cbf.id]);
    const lowPriority = Math.max(...priorities);
    const highPriority = Math.min(...priorities);
    const isPrioritized = (cbf: CompareByField, i: number) : boolean => {
        if (lowPriority === highPriority) {
            return i === 0;
        } else {
            return COMPARE_BY_COLOR_PRIORITIES[cbf.id] === highPriority;
        }
    }
    const kahcs = cbfs.reduce((
        accum: KeysAndHsvColor[],
        cbf: CompareByField,
        cbfi: number
    ) : KeysAndHsvColor[] => {
        const isAccumDefault = !accum[0].keys.k1;
        const fn = COMPARE_BY_COLOR_FNS[cbf.id];
        if (!fn) {
            throw new Error("Color function not found for " + cbf.id);
        }
        const newColors: KeysAndHsvColor[] = accum.flatMap((
            existing: KeysAndHsvColor
        ) => {
            const fieldValues = Object.values(cbf.values);
            const nFieldValues = fieldValues.length;
            return fieldValues.map((cbfv: CompareByFieldValue, vi) => ({
                keys: isAccumDefault ? {
                    k1: {
                        field: cbf.id,
                        value: cbfv.shortName,
                    }
                } : {
                    ...existing.keys,
                    k2: {
                        field: cbf.id,
                        value: cbfv.shortName
                    }
                },
                color: fn(
                    existing.color,
                    cbfv,
                    nFieldValues,
                    vi,
                    isPrioritized(cbf, cbfi))
            }));
        });
        return isAccumDefault ? newColors : [...accum, ...newColors];
    }, [{color: DEFAULT_COLOR, keys: {}}]);
    return setupColorPalette(cbfs, kahcs);
};
