import {createSelector, createSlice, PayloadAction} from '@reduxjs/toolkit';
import {RootState} from '../../app/store';
import {
    ApiData, Area, COMPARE_BY_FIELD_CFGS, CompareByField, CompareByFields,
    Data,
    DataField, DerivedDataValueSet,
    FIELD_CFGS,
    NUMERIC_FIELDS
} from "../../dashboard/config";
import {map, zipObj} from 'ramda';
import masterData from '../../dashboard/data/master.json'

export interface DataState {
    value: Data,
    status: 'idle' | 'loading' | 'failed';
}

const augmentField = (cfg: {[id: string]: any}, f: DataField) : DataField =>
    ({...f, ...cfg[f.id]});

/**
 * Takes (partial) incoming API data and formats it for Redux store
 */
const processData = (data: ApiData) : Data => {
    // @ts-ignore
    const newData: Data = {...data};
    if (!!data.areas) {
        newData['areas'] = {
            byId: map((a: Area) => ({
                ...a,
                link: `/places/us${a.id === 'us' ? '' : '/' + a.id}`
            }), data.areas),
            allIds: Object.keys(data.areas)
        }
    }
    if (!!data.levels) {
        newData['levels'] = {
            byId: data.levels,
            allIds: Object.keys(data.levels)
        }
    }
    if (!!data.fields) {
        newData['fields'] = {
            byId: map(f => augmentField(FIELD_CFGS, f), data.fields),
            allIds: Object.keys(FIELD_CFGS)
        }
    }
    if (!!data.compareByFields) {
        newData['compareByFields'] = {
            byId: map(f => augmentField(COMPARE_BY_FIELD_CFGS, f), data.compareByFields),
            allIds: Object.keys(data.compareByFields)
        }
    }
    return newData;
};

const initialState: DataState = {
    value: {
        // @ts-ignore
        ...processData(masterData),
        values: [],
        topologies: {byAreaAndLevel: {}}
    },
    status: 'idle',
};

export const dataSlice = createSlice({
    name: 'data',
    initialState,
    reducers: {
        setData: (state, action: PayloadAction<any>) => {
            const newData = processData(action.payload);
            state.value.values = [];
            state.value = {...state.value, ...newData};
        },
        clearDataValues: (state) => {
            state.value.values = [];
        }
    }
});

export const { setData, clearDataValues } = dataSlice.actions;
export const selectData = (state: RootState) => state.data.value;
export const selectAreas = (state: RootState) => state.data.value?.areas;
export const selectFields = (state: RootState) => state.data.value?.fields;
export const selectCompareByFields = (state: RootState) :
    {byId: CompareByFields, allIds: string[]} => state.data.value?.compareByFields;
export const selectLevels = (state: RootState) => state.data.value?.levels;
export const selectTopologiesByAreaAndLevel = (state: RootState) =>
    state.data.value?.topologies.byAreaAndLevel;
export const selectYears = (state: RootState) => state.data.value?.years;
export const selectDivisionsByLevel = (state: RootState) =>
    state.data.value?.divisions.byLevel;
export const selectValues = (state: RootState) : DerivedDataValueSet[] =>
    state.data.value?.values;

export const selectAllCompareByFieldDetails = createSelector(
    selectCompareByFields,
    (compareByFields) : CompareByField[] => {
        return compareByFields ? compareByFields.allIds
            .map(id => compareByFields.byId[id])
            : null
    }
);

export const selectNumericFields = createSelector(
    selectFields,
    (fields) : DataField[] => {
        return fields ? fields.allIds
            .map(id => fields.byId[id])
            .filter(f => NUMERIC_FIELDS.includes(f.type)
            ) : null
    }
);

export const selectGeoDivisionFields = createSelector(
    selectLevels,
    (levels) : DataField[] => {
        return Object.values(levels.byId).map(level => ({
            ...level,
            group: 'Geographic',
            type: 'geo'
        }))
    }
);

export default dataSlice.reducer;
