import React from 'react';
import {Provider} from 'react-redux';
import {HashRouter, Link, Route, Routes} from "react-router-dom";
import useDarkMode from 'use-dark-mode';
import {store} from '../app/store';
import {
    Box,
    Button,
    Card,
    CircularProgress,
    CssBaseline,
    IconButton,
    ThemeProvider,
    useMediaQuery
} from '@mui/material';
import {createTheme} from '@mui/material/styles';
import BubblePlot from "./BubblePlot";
import {ParentSize} from "@visx/responsive";
import './Dashboard.scss';
import PlayIcon from '@mui/icons-material/PlayArrow';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import {AxisLabelSource} from "./config";
import {setUseWhatChange} from '@simbathesailor/use-what-changed';
import MapPlot from "./MapPlot";
import LinePlot from "./LinePlot";
import BarPlot from "./BarPlot";
import {BasicShareDialog, ImageDialog, SharePlotDialog} from "./sharing";
import YearSlider from "../features/year/YearSlider";
import {useAppSelector} from "../app/hooks";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import Controls from "../features/controls/Controls";
import DataManager from "../features/data/DataManager";
import Url from './Url';
import {
    isLoaded,
    selectControlFields,
    selectFeatureRanges, selectLegendPosition, selectPlotData,
    selectPlotPresets
} from "./selectors";
import {allValuesNotNull} from "./utils/misc";
import Legend from "./Legend";
import Nav from "./Nav";
import LogoIcon from "./icons/logo.svg";

setUseWhatChange(process.env.NODE_ENV === 'development');
require('typeface-roboto');

const LoadingPlot = ({width, height}) => (
    <div className='loading-plot' style={{width, height}}>
        <CircularProgress variant='indeterminate'
                          size={Math.min(Math.round(width / 7), 80)} />
    </div>
);

const PlotArea = ({plotType,
                      axisLabels, xAxisLabel=null, yAxisLabel=null,
                      disableInteraction = false, showLabel,
                      x, y, levelVar}) => {
    const DumbPlotArea = ({width, height}) => {
        const data = useAppSelector(selectPlotData);
        const featureRanges = useAppSelector(selectFeatureRanges);
        const props = {
            data,
            featureRanges,
            showLabel,
            disableInteraction,
            width,
            height
        };
        switch (plotType.name) {
            case 'bubble': return <BubblePlot {...props} />;
            case 'line': return <LinePlot {...props}  />;
            case 'bar': return <BarPlot {...props}  />;
            default: return <MapPlot {...props} yAxisVar={y} />;
        }
    }
    const loading = !useAppSelector(isLoaded);
    return (
        <ParentSize debounceTime={10} className='bg'>
            {({ width, height }) => {
                return (width + height) > 0 && (
                    <div className='plot' style={{width, height}}>
                        {loading ?
                        <LoadingPlot width={width} height={height} /> :
                        <DumbPlotArea width={width} height={height} /> }
                        {axisLabels && !plotType.hideAxisLabels && (
                            <>
                                <div className='x-axis'>
                                    {xAxisLabel || levelVar}
                                </div>
                                <div className='y-axis' style={{width: height}}>
                                    {yAxisLabel}
                                </div>
                            </>
                        )}
                        {plotType.name !== 'map' && <Legend />}
                    </div>
                )
            }}
        </ParentSize>
    );
};

type DumbDashboardProps = {
    darkMode: any
    plotType: any
    controls: any
}

const DumbDashboard = ({darkMode, plotType, controls}: DumbDashboardProps) => {
    const [genImgOpen, setGenImgOpen] = React.useState(false);
    const [showLabel, setShowLabel] = React.useState(null);

    const computeXAxisLabel = () : string => {
        switch (plotType.xAxisLabelSource) {
            case AxisLabelSource.VAR: return controls.x.name;
            case AxisLabelSource.COMPARE: return controls.compare.map(c => c.name).join('/');
            case AxisLabelSource.YEAR: return 'Year'
            default: return null;
        }
    };

    const xAxisLabel = computeXAxisLabel();
    const yAxisLabel = controls.y?.name;

    const Plot = ({axisLabels, showLabel = null, disableInteraction = false}) => {
        return (
            <PlotArea plotType={plotType}
                      axisLabels={axisLabels}
                      disableInteraction={disableInteraction}
                      showLabel={showLabel}
                      x={controls.x}
                      y={controls.y}
                      xAxisLabel={xAxisLabel}
                      yAxisLabel={yAxisLabel}
                      levelVar={controls.level.name}
            />
        );
    };

    // useWhatChanged([plotType, controls]);

    return (
        <>
            <Nav darkMode={darkMode} ShareDialog={
                ({isOpen, setOpen}) => <SharePlotDialog
                    compares={controls.compare}
                    xAxisLabel={xAxisLabel}
                    yAxisLabel={yAxisLabel}
                    isOpen={isOpen}
                    setOpen={setOpen} />
            }>
                <IconButton className='menu-btn'
                            onClick={() => setGenImgOpen(true)}
                            color="inherit">
                    <PhotoCameraIcon />
                </IconButton>
            </Nav>

            {genImgOpen && <ImageDialog
              isOpen={genImgOpen}
              setOpen={setGenImgOpen}
              label={showLabel}
              setLabel={setShowLabel}
              compares={controls.compare}
              xAxisLabel={xAxisLabel}
              yAxisLabel={yAxisLabel}
              showAxisLabels={!plotType.hideAxisLabels}
              pageInDarkMode={darkMode.value}
              plot={<Plot axisLabels={false}
                          disableInteraction={true}
                          showLabel={showLabel} />} />}
            <div id="dashboard">
                <main>
                    <div className='plot-area'>
                        <Card className='section'>
                            <div id='inner-plot-container'>
                                <Plot axisLabels={true} />
                            </div>
                        </Card>
                    </div>
                    <div className='timeline'>
                        <IconButton className='play' disabled={plotType.name === 'line'}>
                            <PlayIcon />
                        </IconButton>
                        <YearSlider />
                    </div>
                </main>
                <aside>
                    <Controls />
                </aside>
            </div>
        </>
    );
};

const Dashboard = ({darkMode}) => {
    const plotType = useAppSelector(selectPlotType);
    const controls = useAppSelector(selectControlFields);
    const controlsIsReady = allValuesNotNull(controls);
    return (
        <>
            {
                controlsIsReady && plotType &&
                <DumbDashboard darkMode={darkMode}
                               plotType={plotType}
                               controls={controls} />
            }
            <Url />
            <DataManager />
        </>
    );
};

const ExternalLink = ({to, style={}, children}) => (
    <Link to={to}
          target='_blank'
          className='external'
          style={style}
    >
        {children}
    </Link>
);

const SiteScreenshot = ({url}: {url: string}) => (
    <a href={'./' + url} className='screenshot' target='screenshot'>
        <img src={'./' + url} width='100%' alt='screenshot' />
    </a>
);

const DumbHome = ({darkMode, plotPresets}) => (
    <>
        <Nav darkMode={darkMode} ShareDialog={BasicShareDialog} />
        <div id='home'>
            <main>
                <Card className='section'>
                    <img className='logo' src={LogoIcon} alt='Statistipedia logo' />
                    <h2>Welcome to Statistipedia <i>Explore!</i></h2>
                    <p>
                        This is an interactive dashboard.
                    </p>
                    <p style={{textAlign: 'center', margin: '1.5em 0'}}>
                        <Link to='plot' className='btn'>
                            <Button variant="contained">Enter dashboard here</Button>
                        </Link>
                    </p>
                    <p>Or you can try these presets:</p>
                    <table id='presets-table'>
                        <tbody>
                        <tr>
                            <th>Area</th>
                            <th>Description</th>
                        </tr>
                        { plotPresets.map((p) => (
                            <tr key={p.title}>
                                <td>
                                    <ExternalLink to={p.area.link}
                                          style={{whiteSpace: 'nowrap'}}>
                                        {p.area.name}
                                    </ExternalLink>
                                </td>
                                <td>
                                    <Link to={'/' + p.url}>{p.title}</Link>
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                </Card>
            </main>
            <aside>
                <SiteScreenshot url={'img/screenshot1.png'} />
                <SiteScreenshot url={'img/screenshot3.png'} />
                <SiteScreenshot url={'img/screenshot2.png'} />
                <SiteScreenshot url={'img/screenshot4.png'} />
            </aside>
        </div>
    </>
);

const Home = ({darkMode}) => {
    const plotPresets = useAppSelector(selectPlotPresets);
    return <DumbHome darkMode={darkMode} plotPresets={plotPresets} />;
};

const About = ({darkMode}) => (
    <>
        <Nav darkMode={darkMode} ShareDialog={BasicShareDialog} />
        <div id='about'>
            Sun God Ra
        </div>
    </>
);

const DashboardApp = () => {
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    const darkMode = useDarkMode(prefersDarkMode);
    const theme = React.useMemo(
        () =>
            createTheme({
                palette: {
                    mode: darkMode.value ? 'dark' : 'light',
                    primary: {
                        main: darkMode.value ? '#175391' : '#2278CF'
                    }
                },
                breakpoints: {
                    values: {
                        xs: 1,
                        sm: 360,
                        md: 640,
                        lg: 960,
                        xl: 1080,
                    },
                }
            }),
        [darkMode],
    );
    return (
        <Provider store={store}>
            <ThemeProvider theme={theme}>
                <CssBaseline />
                <Box sx={{
                    boxSizing: 'border-box',
                    width: '100%',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    paddingLeft: { xs: 0, xl: '270px' }
                }}>
                    <HashRouter basename='/'>
                        <Routes>
                            <Route path="" element={<Home darkMode={darkMode} />}/>
                            <Route path="about" element={<About darkMode={darkMode} />}/>
                            <Route path="plot" element={<Dashboard darkMode={darkMode} />}/>
                        </Routes>
                    </HashRouter>
                </Box>
            </ThemeProvider>
        </Provider>
    );
};

export default DashboardApp;
