import {createSelector} from "@reduxjs/toolkit";
import {
    selectAreas,
    selectDivisionsByLevel,
    selectFields,
    selectLevels,
    selectNumericFields,
    selectValues,
    selectTopologiesByAreaAndLevel,
    selectCompareByFields,
} from "../features/data/dataSlice";
import {selectActualControlValues} from "../features/controls/controlsSlice";
import {mapObjIndexed} from "ramda";
import {
    Area,
    CompareByField,
    FeatureRanges, LegendCorner, LegendPosition,
    PLOT_PRESETS,
    PlotPreset
} from "./config";
import {chooseLegendPosition, processItemValues} from "./utils/domain";
import {min, max} from 'd3-array';
import {selectYear} from "../features/year/yearSlice";
import {createColorPalette} from "./utils/colors";
import {selectPlotType} from "../features/plotType/plotTypeSlice";
import queryString from "query-string";

export const selectControlFieldValues = createSelector(
    selectActualControlValues,
    selectNumericFields,
    (cv, nfs) => {
        if (!nfs || nfs.length === 0) {
            return cv;
        }
        return {
            ...cv,
            x: cv.x || nfs[0].id,
            y: cv.y || nfs[1].id,
            z: cv.z || nfs[2].id
        }
    }
);

export const selectControlFields = createSelector(
    selectAreas,
    selectLevels,
    selectFields,
    selectCompareByFields,
    selectControlFieldValues,
    (areas, levels, fields, compareByFields, values) => {
        return mapObjIndexed((v, k) => {
            if (!fields || !v) {
                return null;
            } else if (k === 'area') {
                return areas.byId[v];
            } else if (k === 'compare') {
                return v.map(cv => compareByFields.byId[cv]);
            } else if  (k === 'level') {
                return levels.byId[v];
            } else {
                return fields.byId[v];
            }
        }, values)
    }
);

export const selectColorPalette = createSelector(
    selectControlFields,
    (controls) => {
        const cbfs = controls.compare;
        return createColorPalette(cbfs);
    }
);

export const selectCurrentDivisions = createSelector(
    selectDivisionsByLevel,
    selectControlFieldValues,
    (dvsByLevel, cv): any[] => {
        return dvsByLevel[cv.level];
    }
);

export const selectTopologies = createSelector(
    selectTopologiesByAreaAndLevel,
    selectControlFieldValues,
    (tbaal, cvs) => {
        if (Object.keys(tbaal).length > 0) {
            return tbaal[cvs.area][cvs.level];
        } else {
            return null;
        }
    }
);

export const selectArea = createSelector(
    selectAreas,
    selectControlFieldValues,
    (areas, cv) : Area => {
        return areas.byId[cv.area];
    }
);

export const selectCurrentCompareDetails = createSelector(
    selectCompareByFields,
    selectControlFieldValues,
    (cb, cv) : CompareByField[] => {
        return cv.compare.map(c => cb.byId[c]);
    }
);

const summaryDataForAllYears = (vs, area, ds, cf, cp) => {
    const avs = vs.filter(v => v.geoid === area.geoid);
    return processItemValues(avs, cf, ds, cp);
}


const individualDataForAllYears = (vs, area, ds, cf, cp) => {
    const avs = vs.filter(v => v.geoid !== area.geoid);
    return processItemValues(avs, cf, ds, cp);
};

const individualDataIgnoringComparesForAllYears = (vs, area, ds, cf, cp) => {
    const avs = vs.filter(v => v.geoid !== area.geoid);
    return processItemValues(avs, {...cf, compare: []}, ds, cp);
};

export const selectPlotData = createSelector(
    selectPlotType,
    selectValues,
    selectArea,
    selectCurrentDivisions,
    selectControlFields,
    selectYear,
    selectColorPalette,
    (plotType, vs, area, ds, cf, year, cp) => {
        const summaryData = () => {
            const avs = vs.filter(v => v.geoid === area.geoid && v.year === year);
            return processItemValues(avs, cf, ds, cp);
        };
        const individualData = () => {
            const avs = vs.filter(v => v.geoid !== area.geoid && v.year === year);
            return processItemValues(avs, cf, ds, cp);
        };
        const individualDataIgnoringCompares = () => {
            const avs = vs.filter(v => v.geoid !== area.geoid && v.year === year);
            return processItemValues(avs, {...cf, compare: []}, ds, cp);
        };

        if (plotType.name === 'bubble') {
            return individualData();
        } else if (plotType.name === 'line') {
            if (cf.compare.length === 0) {
                return individualDataForAllYears(vs, area, ds, cf, cp);
            } else {
                return summaryDataForAllYears(vs, area, ds, cf, cp);
            }
        } else if (plotType.name === 'bar') {
            if (cf.compare.length === 0) {
                return individualData();
            } else {
                return summaryData();
            }
        } else if (plotType.name === 'map') {
            return individualDataIgnoringCompares();
        } else {
            throw new Error('Unhandled plot type: ' + plotType.name);
        }
    }
);

export const selectFeatureRanges = createSelector(
    selectPlotType,
    selectValues,
    selectArea,
    selectCurrentDivisions,
    selectControlFields,
    selectColorPalette,
    (plotType, vs, area, ds, cf, cp) : FeatureRanges => {
        const data = (() => {
            if (plotType.name === 'map') {
                return individualDataIgnoringComparesForAllYears(vs, area, ds, cf, cp);
            } else if (plotType.name === 'bubble' || cf.compare.length === 0) {
                return individualDataForAllYears(vs, area, ds, cf, cp);
            } else {
                return summaryDataForAllYears(vs, area, ds, cf, cp);
            }
        })();
        const fvs = {
            x: data.map(v => v.x),
            y: data.map(v => v.y),
            z: data.map(v => v.z),
            year: data.map(v => v.year)
        }
        return {
            x: [min(fvs.x), max(fvs.x)],
            y: [min(fvs.y), max(fvs.y)],
            z: [min(fvs.z), max(fvs.z)],
            year: [min(fvs.year), max(fvs.year)]
        };
    }
);

export const selectLegendPosition = createSelector(
    selectPlotType,
    selectPlotData,
    selectFeatureRanges,
    (plotType, data, featureRanges) : LegendPosition | undefined => {
        if (featureRanges.y[0] == null) {
            return undefined;
        } else if (plotType.name === 'bubble' || plotType.name === 'line') {
            return chooseLegendPosition(plotType, featureRanges, data);
        } else if (plotType.name === 'bar') {
            return chooseLegendPosition(plotType, featureRanges, data, LegendCorner.NE);
        } else {
            throw new Error('Unhandled plot type: ' + plotType.name);
        }
    }
);

export const selectPlotPresets = createSelector(
    selectAreas,
    (areas) : PlotPreset[] => PLOT_PRESETS.map(p => ({
        title: p.title,
        area: areas.byId[p.params.area],
        url: 'plot?' + queryString.stringify(p.params, {arrayFormat: 'bracket'})
    })));

export const isLoaded = createSelector(
    selectValues,
    (values) => {
        return values.length > 0;
    }
);
