import React from 'react';
import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {
    clearDataValues,
    selectCompareByFields,
    setData
} from "./dataSlice";
import {DATA_DIR, DataValue, DerivedDataValueSet} from "../../dashboard/config";
import {groupBy, difference} from 'ramda';
import axios from "axios";
import {flattenArrayOfObjects} from "../../dashboard/utils/misc";
import {
    selectControlFields,
    selectControlFieldValues
} from "../../dashboard/selectors";
import {selectControlsLoaded} from "../controls/controlsSlice";
import {selectPlotType} from "../plotType/plotTypeSlice";

type ChunkParams = {
    level: string
    area: string
}

type ValueChunkParams = ChunkParams & {
    compare: string[]
    feature: string
}

type TopologyChunkParams = {

}

type ChunkToFetch<P> = {
    url: string,
    params: P
}

type FetchedChunk<P> = {
    data: DataValue[],
    params: P
}

type FlattenedValueChunk = {
    data: DataValue,
    params: ValueChunkParams
}

export const computeValueChunk = (
    area: string,
    level: string,
    feature: string,
    compare: string[],
    plotType: string
) : ChunkToFetch<ValueChunkParams> => {
    const c = compare.length === 0 || plotType === 'map' ? 'all' : compare.join('_');
    return {
        url: `/pums/${area}/${level}/${feature}/${c}.json`,
        params: {
            area,
            level,
            compare,
            feature,
        }
    };
};

export const computeTopologyChunk = (
    area: string,
    level: string
) : ChunkToFetch<TopologyChunkParams> => {
    return {
        url: `/topologies/${area}.json`,
        params: {
            area,
            level
        }
    };
}

const sortCompares = (compareFields, compares) => {
    return compareFields.allIds.filter(c => compares.includes(c));
};

const DataManager = () => {
    const plotType = useAppSelector(selectPlotType);
    const controls = useAppSelector(selectControlFieldValues);
    const compareFields = useAppSelector(selectCompareByFields);
    const controlsLoaded = useAppSelector(selectControlsLoaded);
    const dispatch = useAppDispatch();

    React.useEffect(() => {
        const _computeChunks = () : ChunkToFetch<ValueChunkParams|TopologyChunkParams>[] => {
            const compares = sortCompares(compareFields, controls.compare);
            const {x, y, z} = controls;
            return [
                ...[x, y, z].map(f =>
                    computeValueChunk(
                        controls.area, controls.level, f, compares, plotType.name)),
                computeTopologyChunk(controls.area, controls.level)
            ];
        }

        const isValueChunk = (c: FetchedChunk<ValueChunkParams|TopologyChunkParams>) : boolean => {
            // @ts-ignore
            return !!c.params.compare;
        }

        const groupByFn = (v: FlattenedValueChunk) => {
            const compareValues = v.params.compare.map(c => v.data[c]);
            return v.data.geoid + "_" + v.data.year + "_" + compareValues.join("_");
        }

        const mergeFeatures = (vs: FlattenedValueChunk[]) : DerivedDataValueSet => {
            const featureSet = [...new Set(vs.flatMap(v => v.params.feature))];
            const allKeys = [...new Set(vs.flatMap(v => Object.keys(v.data)))];
            const nonFeatureSet = difference(allKeys, featureSet);
            const output = {};
            vs.forEach(v => {
                featureSet.forEach(feature => {
                    const featureValue = v.data[feature];
                    if (featureValue != null) {
                        output[feature] = featureValue
                    }
                });
            });
            nonFeatureSet.forEach(key => {      // All non-feature values
                output[key] = vs[0].data[key];  // should be the same
            });
            return output;
        };

        const processValues = (cs: FetchedChunk<ValueChunkParams>[]) : DerivedDataValueSet[] => {
            const vs: FlattenedValueChunk[] = cs.flatMap(
                c => c.data.map(d => ({data: d, params: c.params})));
            const groups: FlattenedValueChunk[][] = Object.values(groupBy(groupByFn, vs));
            return groups.map(mergeFeatures);
        };

        const onChunksFetched = (cs: FetchedChunk<ValueChunkParams|TopologyChunkParams>[]): void => {
            const valueDataArray = cs.filter(isValueChunk) as FetchedChunk<ValueChunkParams>[];
            const valueData = processValues(valueDataArray);
            const otherData = cs.filter(c => !isValueChunk(c)).map(c => c.data);
            const data = { values: valueData, ...flattenArrayOfObjects(otherData) };
            dispatch(setData(data));
        };

        const _fetchChunks = (cs: ChunkToFetch<ValueChunkParams|TopologyChunkParams>[]) => {
            Promise.all(cs.map(c => axios.get(DATA_DIR + c.url))).then((responses) => {
                const fcs = responses.map((r, i) =>
                    ({data: r.data, params: cs[i].params}));
                onChunksFetched(fcs);
            });
        };

        const update = () => {
            return (dispatch, getState) => {
                dispatch(clearDataValues());
                _fetchChunks(_computeChunks());
            }
        };

        if (controlsLoaded)
            dispatch(update());
    }, [dispatch, plotType, controls, controlsLoaded]);  // data left out of deps to prevent infinite loop

    return null;
}

export default DataManager;
