import React from 'react';
import { geoPath, geoAlbersUsa, geoCentroid } from 'd3-geo';
import {animated, useSpring} from 'react-spring';
import { AlbersUsa } from '@visx/geo';
import * as topojson from 'topojson-client';
import {zipObj} from "ramda";
import { hcl } from 'd3-color';
import { scaleLinear } from 'd3-scale';
import { interpolateHcl } from 'd3-interpolate';
import {useAppSelector} from "../app/hooks";
import useSelect from "../hooks/useSelect";
import {DataField, FeatureRanges} from "./config";
import {
    selectArea, selectCurrentDivisions,
    selectTopologies
} from "./selectors";

export type MapPlotProps = {
    data: any[]
    featureRanges: FeatureRanges
    width: number
    height: number
    yAxisVar: DataField
    showLabel?: string
    disableInteraction?: boolean
};

interface FeatureShape {
    type: 'Feature';
    id: string;
    geometry: { coordinates: topojson.Position[][]; type: 'MultiPolygon' };
    properties: { };
}
const projection = geoAlbersUsa();
const usMapOffset: [number, number] = [0.025, 0];

const opacityOptions = {
    selectedOpacity: 1,
    unselectedOpacity: 0.2,
    defaultOpacity: 0.8
};

const strokeFactor = 0.002;

const DumbMapPlot = ({ data, featureRanges, width, height, area, divisions, topologies,
                         showLabel = null, disableInteraction = false }) => {
    const scale = Math.round(Math.min(width, height) * 1.4);
    const topoFeature = topojson.feature(topologies, topologies.objects.data);
    const { features: featColl } = topoFeature as {
        type: 'FeatureCollection';
        features: FeatureShape[];
    };
    const colorScale = scaleLinear()
        .domain(featureRanges.y)
        // @ts-ignore
        .range([
            hcl('rgba(41,122,204,0.1)'),
            hcl('#0265c9')])
        // @ts-ignore
        .interpolate(interpolateHcl)

    const path = geoPath().projection(projection);
    projection.scale(1).translate(area.id === 'us' ? usMapOffset : [0, 0]);
    const b = path.bounds(topoFeature);
    const s = .95 / Math.max((b[1][0] - b[0][0]) / width, (b[1][1] - b[0][1]) / height);
    const t = [(width - s * (b[1][0] + b[0][0])) / 2, (height - s * (b[1][1] + b[0][1])) / 2];
    projection.scale(s).translate([t[0], t[1]]);

    const tempItems = data.map(v => ({
        ...v,
        ...divisions[v.geoid],
        color: colorScale(v.y),
    }));
    const itemMap = zipObj(tempItems.map(d => d.geoid), tempItems);
    const items = featColl.map(f => {
        const c = projection(geoCentroid(f));
        return {
            ...itemMap[f.id],
            scaledX: c[0],
            scaledY: c[1]
        };
    });

    const {makeSelectableAndInteractive, Tooltip} =
        useSelect(items, ['name'], showLabel, disableInteraction);
    return width < 10 ? null : (
        <>
            <svg width={width} height={height}>
                <AlbersUsa<FeatureShape>
                    data={featColl}
                    scale={s}
                    translate={[t[0], t[1]]}
                >
                    {({ features}) =>
                        features
                            .map(({ feature, path }) => {
                                const item = itemMap[feature.id];
                                if (!item) {
                                    return null;
                                }

                                // eslint-disable-next-line react-hooks/rules-of-hooks
                                const [{ animColor }, setAnim] = useSpring(() => ({
                                    animColor: item.color
                                }));

                                setAnim({
                                    animColor: item.color
                                });
                                return makeSelectableAndInteractive([item.name],
                                    <animated.path
                                        className='map-poly'
                                        d={path || ''}
                                        fill={animColor.to(x => x)}
                                        strokeWidth={item.name === 'Alaska' ? 0 : scale * strokeFactor}
                                    />, opacityOptions
                                )
                            })
                    }
                </AlbersUsa>
            </svg>
            <Tooltip />
        </>
    );
};

const MapPlot = (props: MapPlotProps) => {
    const area = useAppSelector(selectArea);
    const topologies = useAppSelector(selectTopologies);
    const divisions = useAppSelector(selectCurrentDivisions);
    return topologies &&
      <DumbMapPlot {...props}
                   area={area}
                   divisions={divisions}
                   topologies={topologies} />
}

export default MapPlot;
